@import './utils';

.projectContents {
    padding: 10px 40px 45px 40px;
    background: $project-box;
    border-radius: 0 0 25px 25px;
    width: 100%;

    animation: fadein 0.5s ease-out;

    @keyframes fadein {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }

    .date {
        margin-left: 2px;
        font-size: 1.1rem;
        color: grey;
        font-weight: 500;
    }

    .personnel {
        margin-left: 25px;
        font-size: 1.1rem;
        color: grey;
        font-weight: 600;
    }

    .tech {
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;

        >p {
            font-size: 1.1rem;
            display: inline-block;
            padding: 3px 8px;
            background-color: lightgrey;
            border-radius: 5px;
            color: firebrick;
            font-weight: 600;
            
            margin-right: 10px;
            margin-top: 5px;
        }
    }

    .carouselwrap {
        display: flex;
        justify-content: center;
        margin-top: 30px;
    }

    pre {
        margin-top: 30px;
        padding: 0 10px;
        font-size: 1.2rem;
        white-space: pre-wrap;

        font-family: inherit;
        line-height: 150%;
    }

    .extrawrap {
        margin-top: 20px;
        padding: 0 10px;
    }

    @media screen and (max-width: 1000px) {
        pre {
            padding: 0;
        }
        .extrawrap {
            padding: 0;
        }
    }

    @media screen and (max-width: 850px) {
        &.hassubname {
            padding-top: 30px;
        }
    }
}